.start-wrapper{
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Orbitron', sans-serif;
    letter-spacing: 0.03em;
}

.content-container{
    text-align: center;
}
.start-info{
    font-size: 1.2rem;
}
.btn{
    cursor: pointer;
    font-family: 'Orbitron', sans-serif;
    border: 1px solid #0B0002;
    color: #0B0002;
    background-color: transparent;
    padding: 0.5vh 1vw;
    transition: 0.3s ease-in-out;
    font-size: 1.2em;
    
}
.btn:hover{
    background-color: #3e3e3e;
    color: #F2EDEA;
    background-color: #14ABBD;
    border: 1px solid #0B0002;
}
.start-links{
    text-decoration: none;
    color: #14ABBD
}
.hud-wrapper{
    height: 9vh;
    background: #14ABBD;
    font-family: 'Orbitron', sans-serif;
    display: grid;
    grid-template-rows: 2fr 1fr;
    padding: 0 1vw;
}

.controls-wrapper{
    position: relative;
    bottom: 0;
    left: 0;
    margin: 1vh 0;
    background-color: #0b000260;
}
.file-selector-wrapper{
    display: inline;
}
.ctl-btn{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #F2EDEA;
    background-color: transparent;
    padding: 0.5vh 1vw;
    transition: 0.6s ease-in-out;
    cursor: pointer;
    height: 100%;
    font-family: 'Orbitron', sans-serif;
    letter-spacing: 0.03em;
    font-size: 1.2em;
    border: none;
    margin: 0vh 0.5vw 0vh 0vw;
    outline: none;
    -ms-user-select: none; /* Internet Explorer */
    -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
    -webkit-user-select: none; /* Chrome, Safari, and Opera */
    -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}
.ctl-btn:hover{
    color: #0B0002;
    background-color: #F2EDEA;
}

.ctl-select-wrapper{
    display: inline;
    height: 100%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #F2EDEA;
    background-color: transparent;
    padding: 0.5vh 1vw;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    font-family: 'Orbitron', sans-serif;
    letter-spacing: 0.03em;
    font-size: 1.2em;
    font-weight: 800;
    margin: 0vh 1vw 0vh 0vw;
    outline: none;
}

.ctl-select{
    width: 30%;
    border: none;
    background-color: transparent;
    outline: none;
    text-transform: uppercase;
    font-size: 1em;
    overflow: hidden;
}

.disable-text-select{
    -ms-user-select: none; /* Internet Explorer */
    -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
    -webkit-user-select: none; /* Chrome, Safari, and Opera */
    -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}

.pad-item-wrapper{
    display: grid;
    grid-template-rows: 1fr 1fr;
    -webkit-justify-content: center;
            justify-content: center;
    text-overflow: clip;
    overflow: hidden;
    width: 50%;
    
}

.pad-text{
    -ms-user-select: none; /* Internet Explorer */
    -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
    -webkit-user-select: none; /* Chrome, Safari, and Opera */
    -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}
.editor-btn{
    color: #F2EDEA;
    background-color: transparent;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    font-size: 2.5em;
    font-weight: 800;
    outline: none;
}
.editor-btn:hover{
    color: #F2EDEA;
    background-color: #0B0002;
    border: 1px solid #0B0002;
}
.editor-wrapper{
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    height: 76vh;
    font-family: 'Orbitron', sans-serif;
    letter-spacing: 0.03em;
}

.pad{
    box-sizing: border-box;
    width: 21%;
    padding: 4vh 1vw 4vh 1vw;
    font-family: 'Orbitron', sans-serif;
    font-weight: 900;
    letter-spacing: 0.03em;
    background-color: #8192a4;
    border-radius: 3%;
    display: inline-block;
    cursor: pointer;
    margin: 2%;
    box-sizing: border-box;
    outline: none;
    box-shadow: 2px 2px 7px #131C20;
    font-size: 2rem;
    text-align: center;

}

.pad:hover{
    box-shadow: 0px 0px 8px 4px #EF7782 !important;
}

.pad-text{
    -ms-user-select: none; /* Internet Explorer */
    -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
    -webkit-user-select: none; /* Chrome, Safari, and Opera */
    -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}

.grid-wrapper{
    position: relative;
    width: 150%;
    max-width: 1170px;
    background: #BBC7D3;
    box-sizing: border-box;
    margin: 0 auto;
}

.grid{
    margin-top: 3vh
}

html {
  /* background: url(/css/img/grid.svg) no-repeat center center fixed; */
  display: table;
  margin: auto;
}

body {
  background-color: #81c9b8;
  margin: 0 auto;
  display: table-cell;
  vertical-align: middle;
}

#body-wrapper {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(26, 48px);
  grid-template-rows: repeat(36, 48px);
  /* background: url(/css/img/grid.png); */
  /* grid-column-gap: 1px;
    grid-row-gap: 1px; */
  background-image: url("https://s3.amazonaws.com/spicedling/ti_XjRYzwmxq9xdeHIW0OlzXrG2mkAQX.svg");
  background-repeat: repeat;
  background-size: 48px 48px;
  width: 100.2%;
}

header {
  grid-column-start: 1;
  grid-column-end: 17;
  grid-row-start: 2;
  grid-row-end: 5;
  background-color: #81aabc;
}

.box {
  grid-column-start: 3;
  grid-column-end: 7;
  grid-row-start: 6;
  grid-row-end: 9;
  background-color: #81aabc;
}

.video-wrapper {
  width: 577px;
  height: 290px;
  position: relative;
  overflow: hidden;
  grid-column-start: 14;
  grid-column-end: 37;
  grid-row-start: 6;
  grid-row-end: 15;
}

.music-player {
  border: 0;
  height: 418px;
  grid-column-start: 9;
  grid-column-end: 15;
  grid-row-start: 9;
  z-index: 3;
}

.video {
  height: 114%;
  width: 102%;
  position: absolute;
  top: 0;
  bottom: 0;
}

.mpc-wrapper {
    grid-column-start:2;
    grid-column-end: 18;
    grid-row-start:3;
    grid-row-end: 27;
    z-index: 3;
}

